}

.API ul li {
  margin-left: 20px;
  padding-top: 10px;
}

.attribution ul {
  font-size: 10px;
}

.attribution div {
  padding-bottom: 10px;
  padding-top: 10px;
}

.attribution ul li {
  margin-left: 20px;
}

a {
  text-decoration: none;
  color: #42a5f5;
}

.link {
  padding-top: 5px;
  padding-bottom: 5px;
}

